.app {
  width: 100%;
  height: 100vh;

  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.bottom-container ul .arrow {
  background-color: rgb(165, 162, 162, 0.4);
  cursor: pointer;
  border-radius: 10px;
  opacity: 0;
  transition: 0.5s ease;
}

.bottom-container ul li {
  cursor: pointer;
}

.bottom-container ul:hover .arrow {
  opacity: 1;
}
